// Breakpoints

$xs_bp: 468px;
$sm_bp: 576px;
$sp_bp: 768px;
$tb_bp: 992px;
$dt_bp: 1200px;
$bdt_bp: 1330px;

// Breackpoints Use in mixins
$low-mobile-breackpoint: 375px;
$mobile-breackpoint: 1024px;
$tablet-breackpoint: 1310px;

// Colors
$primary: #98c063;
// $primary: #ac28f5;
$primaryHover: #b1cf8a;
// $primaryHover: #c55fff;
$secondary: #ccff74;
$success: #8ad52a;
// $btnPrimary: #98c063;
// $btnSecondary: #b1cf8a;
$pink: #ff809e;
$green: #3feb7e;
$dark: #444444;
$text: #555;
$text-primary: #efefef;
$gray: #999;
$white: #fff;
$black: #0d0d0d;
$light-gray: #707378;

$gradient-yellow: linear-gradient(180deg, #ffd770 0%, #ff9f15 100%);
$gradient-pink: linear-gradient(180deg, #fdbd91 0%, #f66daf 100%);
$gradient-green: linear-gradient(180deg, #beec82 0%, #7bc719 100%);

:root {
  --theme-body-color: #160e24;
  --theme-card-color: #181a1e;
  --thame-card-shadow: transparent;
  --theme-border-color: #142149;
  --theme-text-primary: #efefef;
  --theme-text-secondary: #b7b7b7;
  --theme-text-gray: #707378;
  --thame-text-light-gray: #efefef;
  --thame-text-dark-gray: #efefef;
  --theme-text-white: #fff;
  --theme-box-shadow: rgba(5, 5, 5, 0.3);
  --theme-modal-bg: #160e24;
  --thame-modal-text-gray: #707378;
  --thame-modal-backing-bg: #181a1e;
  --thame-socials-primary: #fff;
  --thame-socials-gray: #707378;
  --thame-btn-square: #313339;
  --thame-close-icon: rgba(255, 255, 255, 0.6);
  --thame-card-btn: #0d0d0d;
  --thame-card-btn-text: #fff;
  --thame-card-border-color: transparent;
  --thame-card-no-image: #707378;
  --thame-card-bg: #071235;
  --thame-card-no-image-bg: #0c1e52;
  --thame-disabled-btn-text: #343639;
  --theme-avatar-bg: #ac28f5;
  --theme-avatar-text: #fff;
  --theme-nfts-fade-light: rgba(5, 5, 5, 0.1);
  --theme-nfts-fade: rgba(5, 5, 5, 0.95);
  --theme-nfts-fade-footer: #050505;
}

.light-mode {
  --theme-body-color: rgb(231, 226, 240);
  --theme-card-color: #ffffff;
  --thame-card-shadow: #c5c5c5;
  --theme-border-color: #e8e8e8;
  --theme-text-primary: #000;
  --theme-text-secondary: #555;
  --theme-text-gray: #999;
  --thame-text-light-gray: #999;
  --thame-text-dark-gray: #444;
  --theme-text-white: #000;
  --theme-box-shadow: rgba(197, 197, 197, 0.25);
  --theme-modal-bg: #fff;
  --thame-modal-backing-bg: #f3f3f3;
  --thame-socials-primary: #ac28f5;
  --thame-socials-gray: #ac28f5;
  --thame-btn-square: #999;
  --thame-close-icon: rgba(29, 29, 29, 0.6);
  --thame-card-btn: #fbfbfb;
  --thame-card-btn-text: #555;
  --thame-card-border-color: #e7e7e7;
  --thame-card-bg: #fff;
  --thame-card-no-image: #999;
  --thame-card-no-image-bg: #ededed;
  --thame-disabled-btn-text: #c2c3c5;
  --theme-avatar-bg: #000;
  --theme-avatar-text: #ac28f5;
  --theme-nfts-fade-light: rgba(241, 238, 238, 0.1);
  --theme-nfts-fade: rgba(221, 217, 217, 0.95);
  --theme-nfts-fade-footer: #eee7e7;
}

@mixin cardHead {
  display: grid;
  grid-template-columns: 50px 1fr;
  align-items: center;
  grid-gap: 16px;
  margin-bottom: 19px;

  h4 {
    margin-bottom: 0;
    color: var(--theme-text-primary);

    i {
      font-size: 9px;
      position: relative;
      top: -3px;
    }
  }

  span {
    font-size: 16px;
    line-height: 20px;
    color: #999;

    i {
      position: relative;
      top: 2px;
    }
  }

  img {
    max-width: 100%;
  }
}

// Flex
@mixin flex-align($align-position) {
  display: flex;
  align-items: $align-position;
}

@mixin flex-justify($justify-position) {
  display: flex;
  justify-content: $justify-position;
}

@mixin flex-justify-align($justify-position, $align-position) {
  display: flex;
  justify-content: $justify-position;
  align-items: $align-position;
}

/* Breakpoint mixins */
@mixin media-tablet {
  @media (max-width: $tablet-breackpoint) {
    @content;
  }
}

@mixin media-mobile {
  @media (max-width: $mobile-breackpoint) {
    @content;
  }
}

@mixin media-low-mobile {
  @media (max-width: $low-mobile-breackpoint) {
    @content;
  }
}

.modal {
  padding: 30px;

  .statusIcon {
    display: block;
    margin: -30px auto -50px;
    width: 219px;
    height: 187px;
  }

  &__title {
    text-align: center;
    font-weight: 600;
    font-size: 24px;
    line-height: 20px;
    margin: 0 0 30px;
    color: var(--thame-text-dark-gray);
  }
}

.walletInfoWrapp {
  display: flex;
  min-width: 155px;
  position: relative;

  @include media-mobile {
    min-width: 127px;

    .btnConnect {
      min-height: 40px;
      padding: 3px 10px;
      font-size: 12px;
    }
  }
}

.walletInfo {
  @include flex-justify-align(center, center);
  padding-right: 25px;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  outline: none;
  border: none;

  &::after {
    content: '';
    position: absolute;
    top: calc((100% - 12px) / 2);
    right: 5px;
    width: 8px;
    height: 8px;
    border-left: 8px solid transparent;
    border-top: 8px solid var(--thame-text-dark-gray);
    transform: rotate(135deg);
    transition: all 0.3s ease-in-out;
    @include media-mobile {
      top: calc((100% - 10px) / 2);
      width: 10px;
      height: 10px;
      border-left: 10px solid transparent;
      border-top: 10px solid var(--thame-text-dark-gray);
    }
  }

  &__avatar {
    width: 36px;
    height: 36px;
    // border: 1px solid $primary;
    // filter: drop-shadow(0px 0px 8px rgba(172, 40, 245, 0.5));
    border-radius: 50%;
    background-color: var(--theme-body-color);
    margin-right: 8px;
    cursor: pointer;

    @include media-mobile {
      margin-left: auto;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      // border-radius: 50%;
      background-color: var(--theme-body-color);
    }
  }

  p {
    width: fit-content;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.3px;
    margin-bottom: 0;
    transition: all 0.3s ease-in-out;
  }
}

.userMenu {
  position: absolute;
  top: 52px;
  right: 0;
  width: 163px;
  background-color: var(--theme-body-color);
  border: 1px solid var(--theme-border-color);
  border-radius: 10px;
  margin: 0;
  list-style-type: none;

  @include media-mobile {
    right: 0;
  }

  & li {
    border-bottom: 1px solid var(--theme-border-color);

    &:last-child {
      border-bottom: none;
    }

    & a {
      display: block;
      font-size: 14px;
      line-height: 1;
      width: 100%;
      height: 100%;
      padding: 12px 14px;
      transition: color 0.3s ease-in-out;

      &:hover {
        color: $primaryHover;
        cursor: pointer;
        transition: color 0.3s ease-in-out;
      }
    }
  }

  &__disconnectBtn {
    @include flex-align(center);
    margin: 0;
    background-color: transparent;
    outline: none;
    border: none;
    font-weight: normal;
    font-size: 14px;
    line-height: 1;
    padding: 12px 14px;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease-in-out;

    &:hover {
      color: $primaryHover;
      cursor: pointer;
      transition: all 0.3s ease-in-out;

      path {
        stroke: $primaryHover;
        transition: all 0.3s ease-in-out;
      }
    }

    & > div {
      width: 15px;
      min-width: 15px;
      height: 15px;
      margin-right: 8px;

      svg {
        display: block;
        width: 100%;

        & path {
          stroke: var(--theme-text-primary);
          transition: all 0.3s ease-in-out;
        }
      }
    }
  }
}

.walletList {
  list-style: none;
  display: grid;
  grid-gap: 20px;
  margin: 0;

  li {
    button {
      padding: 16px;
      height: auto;
      display: grid;
      grid-template-columns: 24px 1fr;
      align-items: center;
      grid-gap: 16px;
      text-align: left;
      background: var(--theme-modal-bg);
      color: var(--theme-text-white);
      border: 1px solid var(--theme-border-color);
      border-radius: 10px;
      font-weight: 500;

      &:hover,
      &:focus {
        background: var(--theme-modal-bg);
      }

      img {
        max-width: 100%;
      }
    }
  }
}

.btn {
  &__okay {
    margin: 0 auto;
    display: block;
    width: 100%;
    max-width: 200px;
    padding: 19px 30px;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 30px;
  }
}
