// Breakpoints

$xs_bp: 468px;
$sm_bp: 576px;
$sp_bp: 768px;
$tb_bp: 992px;
$dt_bp: 1200px;
$bdt_bp: 1330px;

// Breackpoints Use in mixins
$low-mobile-breackpoint: 375px;
$mobile-breackpoint: 1024px;
$tablet-breackpoint: 1310px;

// Colors
$primary: #98c063;
// $primary: #ac28f5;
$primaryHover: #b1cf8a;
// $primaryHover: #c55fff;
$secondary: #ccff74;
$success: #8ad52a;
// $btnPrimary: #98c063;
// $btnSecondary: #b1cf8a;
$pink: #ff809e;
$green: #3feb7e;
$dark: #444444;
$text: #555;
$text-primary: #efefef;
$gray: #999;
$white: #fff;
$black: #0d0d0d;
$light-gray: #707378;

$gradient-yellow: linear-gradient(180deg, #ffd770 0%, #ff9f15 100%);
$gradient-pink: linear-gradient(180deg, #fdbd91 0%, #f66daf 100%);
$gradient-green: linear-gradient(180deg, #beec82 0%, #7bc719 100%);

:root {
  --theme-body-color: #160e24;
  --theme-card-color: #181a1e;
  --thame-card-shadow: transparent;
  --theme-border-color: #142149;
  --theme-text-primary: #efefef;
  --theme-text-secondary: #b7b7b7;
  --theme-text-gray: #707378;
  --thame-text-light-gray: #efefef;
  --thame-text-dark-gray: #efefef;
  --theme-text-white: #fff;
  --theme-box-shadow: rgba(5, 5, 5, 0.3);
  --theme-modal-bg: #160e24;
  --thame-modal-text-gray: #707378;
  --thame-modal-backing-bg: #181a1e;
  --thame-socials-primary: #fff;
  --thame-socials-gray: #707378;
  --thame-btn-square: #313339;
  --thame-close-icon: rgba(255, 255, 255, 0.6);
  --thame-card-btn: #0d0d0d;
  --thame-card-btn-text: #fff;
  --thame-card-border-color: transparent;
  --thame-card-no-image: #707378;
  --thame-card-bg: #071235;
  --thame-card-no-image-bg: #0c1e52;
  --thame-disabled-btn-text: #343639;
  --theme-avatar-bg: #ac28f5;
  --theme-avatar-text: #fff;
  --theme-nfts-fade-light: rgba(5, 5, 5, 0.1);
  --theme-nfts-fade: rgba(5, 5, 5, 0.95);
  --theme-nfts-fade-footer: #050505;
}

.light-mode {
  --theme-body-color: rgb(231, 226, 240);
  --theme-card-color: #ffffff;
  --thame-card-shadow: #c5c5c5;
  --theme-border-color: #e8e8e8;
  --theme-text-primary: #000;
  --theme-text-secondary: #555;
  --theme-text-gray: #999;
  --thame-text-light-gray: #999;
  --thame-text-dark-gray: #444;
  --theme-text-white: #000;
  --theme-box-shadow: rgba(197, 197, 197, 0.25);
  --theme-modal-bg: #fff;
  --thame-modal-backing-bg: #f3f3f3;
  --thame-socials-primary: #ac28f5;
  --thame-socials-gray: #ac28f5;
  --thame-btn-square: #999;
  --thame-close-icon: rgba(29, 29, 29, 0.6);
  --thame-card-btn: #fbfbfb;
  --thame-card-btn-text: #555;
  --thame-card-border-color: #e7e7e7;
  --thame-card-bg: #fff;
  --thame-card-no-image: #999;
  --thame-card-no-image-bg: #ededed;
  --thame-disabled-btn-text: #c2c3c5;
  --theme-avatar-bg: #000;
  --theme-avatar-text: #ac28f5;
  --theme-nfts-fade-light: rgba(241, 238, 238, 0.1);
  --theme-nfts-fade: rgba(221, 217, 217, 0.95);
  --theme-nfts-fade-footer: #eee7e7;
}

@mixin cardHead {
  display: grid;
  grid-template-columns: 50px 1fr;
  align-items: center;
  grid-gap: 16px;
  margin-bottom: 19px;

  h4 {
    margin-bottom: 0;
    color: var(--theme-text-primary);

    i {
      font-size: 9px;
      position: relative;
      top: -3px;
    }
  }

  span {
    font-size: 16px;
    line-height: 20px;
    color: #999;

    i {
      position: relative;
      top: 2px;
    }
  }

  img {
    max-width: 100%;
  }
}

// Flex
@mixin flex-align($align-position) {
  display: flex;
  align-items: $align-position;
}

@mixin flex-justify($justify-position) {
  display: flex;
  justify-content: $justify-position;
}

@mixin flex-justify-align($justify-position, $align-position) {
  display: flex;
  justify-content: $justify-position;
  align-items: $align-position;
}

/* Breakpoint mixins */
@mixin media-tablet {
  @media (max-width: $tablet-breackpoint) {
    @content;
  }
}

@mixin media-mobile {
  @media (max-width: $mobile-breackpoint) {
    @content;
  }
}

@mixin media-low-mobile {
  @media (max-width: $low-mobile-breackpoint) {
    @content;
  }
}

.Modal-container {
  border-radius: 20px;
  overflow: hidden;
  border: 1px solid var(--theme-border-color);
  padding: 0;

  &.No-border-title {
    .ant-modal-header {
      border-bottom: none;
    }

    .ant-modal-body {
      padding: 0 24px 24px;
    }
  }

  .ant-modal-header {
    padding: 0;
    background-color: var(--theme-modal-bg);
    border-bottom: 1px solid var(--theme-border-color);
  }

  .ant-modal-title {
    padding: 31px 30px 27px 30px;
    font-weight: 600;

    @media (max-width: $tb_bp) {
      padding: 17px 20px;
    }
  }

  .ant-modal-close {
    top: 21px;
    right: 18px;
    opacity: 0.6;
    color: var(--theme-text-white);

    &:hover {
      opacity: 1;
      color: var(--theme-text-white);
    }

    .ant-modal-close-x {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 15px;
      height: 15px;
      line-height: normal;
    }

    .icon-close {
      font-size: 15px;
    }
  }
}

.ant-modal-mask {
  background: rgba(34, 18, 71, 0.3);
}

// Any type: Solid Background
.Modal-container.solid-bg {
  .ant-modal-body {
    padding: 0;
  }

  .ant-modal-content {
    background: var(--theme-body-color);
  }
}

// Padding Reset for Ant Modal
.Modal-container.padding-reset {
  .ant-modal-body {
    padding: 0;
  }
}

// Background Patterns
.bg-pattern-1,
.bg-pattern-2 {
  background-repeat: no-repeat;
  background-position-y: bottom;
}

.bg-pattern-1 {
  background-image: url('../../../assets/images/bg_modal_dark--pattern1.png');
}

.light-mode .bg-pattern-1 {
  background-image: url('../../../assets/images/bg_modal_white--pattern1.png');
}

.bg-pattern-2 {
  background-image: url('../../../assets/images/bg_modal_dark--pattern2.png');
}

.light-mode .bg-pattern-2 {
  background-image: url('../../../assets/images/bg_modal_white--pattern2.png');
}

// Status Icon Patterns
.status-icon-pattern-1,
.status-icon-pattern-2 {
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}

.status-icon-pattern-1 {
  background-image: url('../../../assets/images/status_icon_dark--pattern1.png');
}

.light-mode .status-icon-pattern-1 {
  background-image: url('../../../assets/images/status_icon_white--pattern1.png');
}

.status-icon-pattern-2 {
  background-image: url('../../../assets/images/status_icon_dark--pattern2.png');
}

.light-mode .status-icon-pattern-2 {
  background-image: url('../../../assets/images/status_icon_white--pattern2.png');
}
